@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: linear-gradient(to top right, #00c6fb 0%, #005bea 100%);
}
.App{
  background-color: #232323;
  min-height:500px;
  width:350px;
  margin:7% auto;
  border-radius: 6px;
}
#to-do-form{
  border:1px solid transparent;
}
#to-do-form input{
  background-color: #282828;
  border:0;
  width:200px;
  height:50px;
  padding:0 20px;
  margin:20px;
  margin-top: 35px;
  font-size:11px;
  border-radius: 5px;
  color: white;
  letter-spacing: 0.2em;
  font-family: 'Open Sans', sans-serif;
  border-left: 3px solid #3498DB;

}
#to-do-form input::-webkit-input-placeholder{
  color: white;
}
#to-do-form input:-ms-input-placeholder{
  color: white;
}
#to-do-form input::-ms-input-placeholder{
  color: white;
}
#to-do-form input::placeholder{
  color: white;
}
#to-do-form input:focus,#to-do-form button:focus{
  outline:none;
}
#to-do-form button{
  height:43px;
  width:43px;
  font-size: 25px;
  border:0;
  border-radius:50%;
  background:linear-gradient(to top right, #1a2980, #26d0ce);
  cursor:pointer;
  font-weight: bolder;
  color:white;
  transition: 0.5s;
}
#to-do-form button:hover{
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.list{
  color:#fff;
  width:300px;
  background: linear-gradient(to bottom left, #00c6fb 0%, #005bea 100%);
  height:45px;
  margin:20px auto;
  border-radius: 26px;
  box-shadow: 0px 0px 13px 1px #191919;
}
.list p{
  padding:10px;

  position: relative;
}

.list p span{
  position:absolute;
  right:10px;
  cursor: pointer;
}
.faicons{
  margin:0 5px;
  margin-right: 1em;
  font-size:15px;
}

.list p input{
  background-color: transparent;
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'Open Sans', sans-serif;
  border:0;
  color:#fff;
  font-size: 14px;
  margin-left: 1em;
  letter-spacing: 0.05em;
  text-transform: capitalize;
}

.list p input:focus{outline: none;}

