@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap');
body{
  background: linear-gradient(to top right, #00c6fb 0%, #005bea 100%);
}
.App{
  background-color: #232323;
  min-height:500px;
  width:350px;
  margin:7% auto;
  border-radius: 6px;
}
#to-do-form{
  border:1px solid transparent;
}
#to-do-form input{
  background-color: #282828;
  border:0;
  width:200px;
  height:50px;
  padding:0 20px;
  margin:20px;
  margin-top: 35px;
  font-size:11px;
  border-radius: 5px;
  color: white;
  letter-spacing: 0.2em;
  font-family: 'Open Sans', sans-serif;
  border-left: 3px solid #3498DB;

}
#to-do-form input::placeholder{
  color: white;
}
#to-do-form input:focus,#to-do-form button:focus{
  outline:none;
}
#to-do-form button{
  height:43px;
  width:43px;
  font-size: 25px;
  border:0;
  border-radius:50%;
  background:linear-gradient(to top right, #1a2980, #26d0ce);
  cursor:pointer;
  font-weight: bolder;
  color:white;
  transition: 0.5s;
}
#to-do-form button:hover{
  transform: rotate(-90deg);
}
