@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');

.list{
  color:#fff;
  width:300px;
  background: linear-gradient(to bottom left, #00c6fb 0%, #005bea 100%);
  height:45px;
  margin:20px auto;
  border-radius: 26px;
  box-shadow: 0px 0px 13px 1px #191919;
}
.list p{
  padding:10px;

  position: relative;
}

.list p span{
  position:absolute;
  right:10px;
  cursor: pointer;
}
.faicons{
  margin:0 5px;
  margin-right: 1em;
  font-size:15px;
}

.list p input{
  background-color: transparent;
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'Open Sans', sans-serif;
  border:0;
  color:#fff;
  font-size: 14px;
  margin-left: 1em;
  letter-spacing: 0.05em;
  text-transform: capitalize;
}

.list p input:focus{outline: none;}
